.header {
    background: #010103;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 8px 7%;
    border-bottom: 0.2rem solid #ffc107;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1000;

}

.header .logo h3 {
    font-size: 2rem;
    color: #fff;
}

.header .navbar a {
    margin: 0 1rem;
    font-size: 1.6rem;
    color: #fff;
}

.header .navbar a:hover {
    color: #ffc107;
    border-bottom: 0.1rem solid #ffc107;
    padding-bottom: 0.5rem;
}

.header .icons {
    display: flex;
}

.header .icons .asd {
    color: #fff;
    cursor: pointer;
    font-size: 2.5rem;
    margin-left: 2rem;


}

.header .icons .asd:hover {
    color: #ffc107;
}

#menu-btn {
    display: none;
}

.header .search-form {
    position: absolute;
    top: 50px;
    right: 7%;
    background: #fff;
    width: 50rem;
    height: 5rem;
    display: flex;
    align-items: center;
    transform: scaleY(0);
    transform-origin: top;
}

.header .search-form.active {
    transform: scaleY(1);

}

.header .search-form input {
    height: 100%;
    width: 100%;
    font-size: 1.6rem;
    color: #13131a;
    padding: 1rem;
    text-transform: none;
}

.header .search-form label {
    cursor: pointer;
    font-size: 2.2rem;
    margin-right: 1.5rem;
    color: #13131a;
}

.header .search-form label:hover {
    color: #ffc107;
}

.header .cart-items-container {
    position: absolute;
    top: 50px;
    right: -100%;
    height: 600px;
    width: 35rem;
    background: #fff;
    padding: 0 1.5rem;
}

.header .cart-items-container.active {
    right: 0;
}

.header .cart-items-container .cart-item {
    position: relative;
    margin: 2rem 0;
    display: flex;
    align-items: center;
    gap: 1.5rem;
}

.header .cart-items-container .cart-item .fa-times {
    position: absolute;
    top: 1rem;
    right: 1rem;
    font-size: 2rem;
    cursor: pointer;
    color: #13131a;

}

.header .cart-items-container .cart-item .fa-times:hover {
    color: #ffc107;
}

.header .cart-items-container .cart-item img {
    height: 7rem;
}

.header .cart-items-container .cart-item .content h3 {
    font-size: 2rem;
    color: #13131a;
    padding-bottom: 0.5rem;

}

.header .cart-items-container .cart-item .content .price {
    font-size: 1.5rem;
    color: #ffc107;
}

.header .cart-items-container .btn {
    width: 100%;
    text-align: center;
}

@media(max-width: 991px) {
    .header {
        padding: 1.5rem 2rem;
    }
}

@media(max-width: 768px) {
    #menu-btn {
        display: inline-block;

    }

    .header .navbar {

        position: absolute;

        top: 70px;
        right: -100%;
        background: #000;
        width: 30rem;
        height: calc(100vh - 9.5rem);
    }

    .header .navbar.active {
        right: 0;
    }

    .header .navbar a {
        color: #fff;
        display: block;
        margin: 1.5rem;
        padding: 0.5rem;
        font-size: 2rem;
    }
}