.home {
    min-height: 100vh;
    display: flex;
    align-items: center;
    background: linear-gradient(90deg, rgb(0 0 0 /90%), rgb(68 68 68 /35%)),
        url(../pics/b.jpg) no-repeat;
    background-size: cover;
    background-position: center;
}

.home .content {
    max-width: 60rem;
}

.home span {
    color: #ffc107;
    text-transform: uppercase;
}

.home .content h3 {
    font-size: 6rem;
    text-transform: uppercase;
    color: #fff;

}

.home .content p {
    font-size: 2rem;
    font-weight: lighter;
    line-height: 1.8;
    padding: 1rem 0;
    color: #eee;

}

@media(max-width: 768px) {
    .home {
        background-position: left;
        justify-content: center;
        text-align: center;
    }

    .home .content h3 {
        font-size: 4.5rem;

    }

    .home .content p {
        font-size: 1.5rem;
    }
}